*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

p:last-child {
  margin-bottom: 0;
}

ul, ol, a {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hiddenvisualy {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

button {
  text-align: center;
  cursor: pointer;
  border-radius: 100px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21429;
  list-style: none;
}

:root {
  --light-theme-fill: #f8f8f8;
  --light-theme-main-text: #050505;
  --light-theme-switcher-btn: #cecdcd;
  --dark-theme-fill: #161616;
  --dark-theme-main-text: #fff;
  --dark-theme-switcher-btn: #f3f3f3;
  --green-btn: #9bb537;
  --star-primal: #eea10c;
  --star-second-light: #d9d9d9;
  --star-second-dark: #3c3b3b;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  color: var(--light-theme-main-text);
  background-color: var(--light-theme-fill);
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 100px;
    padding-right: 100px;
  }
}

@media screen and (min-width: 375px) {
  .container-mobile {
    max-width: 200px;
    min-height: 100vh;
    margin-left: auto;
    margin-right: auto;
  }
}

.header-map {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.list-navigation {
  align-items: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: flex;
}

.page-home {
  margin-right: 16px;
}

.header-logo {
  letter-spacing: -.64px;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.13;
}

.logo-first {
  color: var(--green-btn);
}

.head-buttonbox {
  align-items: center;
  display: flex;
}

.switch-theme {
  display: none;
}

.burger-open {
  background: none;
  border: none;
  margin: 0 0 0 14px;
  padding: 0;
}

.shopping-cart {
  width: 24px;
  height: 24px;
}

.icon-gamburger {
  stroke: var(--light-theme-main-text);
}

.icon-gamburger:hover, .icon-gamburger:focus {
  stroke: var(--green-btn);
}

.header-menu-burger {
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  padding: 0;
}

.page-home, .page-favorites {
  position: relative;
}

.page-home:hover, .page-home:focus, .page-favorites:hover, .page-favorites:focus {
  color: var(--green-btn);
  transition: color var(--transition-dur-and-func);
}

.page-home:before, .page-favorites:before {
  content: "";
  width: 100%;
  height: 1.8px;
  background-color: var(--green-btn);
  transition: transform var(--transition-dur-and-func);
  border-radius: 4px;
  display: block;
  position: absolute;
  top: -2px;
  left: 0;
  transform: scale(0);
}

.page-home:after, .page-favorites:after {
  content: "";
  width: 100%;
  height: 1.8px;
  background-color: var(--green-btn);
  transition: transform var(--transition-dur-and-func);
  border-radius: 4px;
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scale(0);
}

.page-navigation .current {
  color: var(--green-btn);
}

.shopping-link {
  fill: none;
  stroke: #050505;
}

.shopping-link:hover .shopping-cart, .shopping-link:focus .shopping-cart {
  stroke: var(--green-btn);
  fill: none;
  transition: var(--transition-dur-and-func);
}

.container-mobile {
  z-index: 666;
  width: 200px;
  height: 100vh;
  background-color: var(--green-btn);
  transform-origin: bottom;
  transition: transform 1000ms var(--transition-dur-and-func);
  margin-bottom: auto;
  padding-top: 200px;
  padding-left: 73px;
  padding-right: 74px;
  position: fixed;
  top: 0;
  right: 0;
  transform: scaleY(0);
}

.container-mobile.is-open {
  transform-origin: top;
  transition: transform 1000ms var(--transition-dur-and-func);
  transform: scaleY(1);
}

.mobile-nav-link {
  color: var(--dark-theme-main-text);
  text-align: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.burger-close {
