.backdrop {
  transition: opacity var(--transition-dur-and-func);
  opacity: 1;
  background: rgba(46, 47, 66, .4);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

