  .container-switch-mobile {
    width: 46px;
    height: 20px;
    background: var(--light-theme-switcher-btn);
    border-radius: 24px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 193px;
    display: flex;
  }

  .switch {
    width: 46px;
    height: 20px;
    display: inline-block;
    position: relative;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider-header {
    cursor: pointer;
    background-color: #ccc;
    transition: all .4s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .slider-header:before {
    content: "";
    height: 20px;
    width: 20px;
    box-shadow: 0 0px 15px var(--light-theme-switcher-btn);
    background: #fff center no-repeat;
    margin: auto 0;
    transition: all .4s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
  }

  input:checked + .slider-header {
    background-color: var(--light-theme-switcher-btn);
  }

  input:focus + .slider-header {
    box-shadow: 0 0 1px #cecdcd;
  }

  input:checked + .slider-header:before {
    background: #fff center no-repeat;
    transform: translateX(24px);
  }

  .slider-header.round {
    border-radius: 24px;
  }

  .slider-header.round:before {
    border-radius: 50%;
  }

  .container-switch-header {
    display: none;
  }

  .header-buttonbox {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .header-logo {
    margin-left: 0;
    margin-right: 182px;
